<script>
  import { WebCam } from 'vue-cam-vision'
  // import EventService from './services/EventService.js'
  import FastSpeedtest from 'fast-speedtest-api'

  export default {
    components: {
      WebCam,
    },
    props: {
      source: String,
    },
    data: () => ({
      drawer: false,
      cameraSwitch: true,
      microphoneSwitch: true,
      captures: [],
      imgReport: [],
      frontCam: false,
      webcam: null,
      img: null,
      camera: false,
      deviceId: null,
      devices: [],
      mtype: null,
      media: null,
      device: null,
      speed: null,
      dialog: false,
      speedresult: '',
      speedresultExtra: '',
      speedflag: false,
    }),
    watch: {
      cameraSwitch (newValue) {
        if (!newValue) {
          this.$refs.webcam.stop()
        } else {
          this.$refs.webcam.start()
        }
      },
      microphoneSwitch (newValue) {
        if (!newValue) {
          this.$refs.player.srcObject = null
        }
      },
    },
    created () {
      this.$vuetify.theme.dark = false
      this.getSpeedResults()
    },
    mounted () {
      const audio = this.$refs.player
      const constraints = {
        audio: true,
        video: false,
      }
      navigator.mediaDevices.getUserMedia(constraints)
        .then(media => {
          this.media = media
          if (this.microphoneSwitch) {
            audio.srcObject = media
          }
        })
    },
    methods: {
      async getSpeedResults () {
        const speedtest = new FastSpeedtest({
          token: 'YXNkZmFzZGxmbnNkYWZoYXNkZmhrYWxm', // required
          verbose: false, // default: false
          timeout: 10000, // default: 5000
          https: true, // default: true
          urlCount: 5, // default: 5
          bufferSize: 8, // default: 8
          unit: FastSpeedtest.UNITS.Mbps, // default: Bps
        })
        speedtest.getSpeed().then(s => {
          var speed = parseFloat(s).toFixed(2)
          this.$set(this, 'speed', speed)
          if (speed < 30) {
            this.speedresult = 'Poor Connectivity'
            this.speedresultExtra = 'Are you using the device and internet connection that you will be using at the time of your visit?'
            this.speedflag = false
          } else {
            this.speedresult = 'Good Connectivity'
            this.speedresultExtra = 'Make sure you use the same device and internet connection at the time of your visit?'
            this.speedflag = true
          }
          // res.json({"Speed": speed})
        }).catch(e => {
          // console.error(e.message)
          this.$set(this, 'speed', 0)
          this.speedresult = 'Poor Connectivity'
          this.speedresultExtra = 'Are you using the device and internet connection that you will be using at the time of your visit?'
          this.speedflag = false
        })
        // Use the eventService to call the getEventSingle() method
        // EventService.getSpeed()
        //   .then(
        //     event => {
        //       this.$set(this, 'speed', event.Speed)
        //       if (parseFloat(this.speed) < 30) {
        //         this.speedresult = 'Poor Connectivity'
        //         this.speedresultExtra = 'Are you using the device and internet connection that you will be using at the time of your visit?'
        //         this.speedflag = false
        //       } else {
        //         this.speedresult = 'Good Connectivity'
        //         this.speedresultExtra = 'Make sure you use the same device and internet connection at the time of your visit?'
        //         this.speedflag = true
        //       }
        //     },
        //   )
      },
    },
  }
</script>
